import { User } from '../interfaces/user.interface';

export interface SearchState {
  from?: string;
  to?: string;
  journeyDate?: string;
  departureTime?: string;
}

export interface BookingState {
  tripId?: number;
  seat?: string;
  [key: string]: unknown;
}

export interface UserState {
  data?: {
    token?: string;
    user?: User;
    [key: string]: unknown;
  };
  [key: string]: unknown;
}

export const updateUser = (state: UserState, payload: UserState): UserState => {
  return {
    ...state,
    user: payload,
  };
};

export const updateSearch = (state: { search?: SearchState }, payload: SearchState): { search: SearchState } => {
  return {
    ...state,
    search: payload,
  };
};

export const updateBooking = (state: { booking?: BookingState }, payload: BookingState): { booking: BookingState } => {
  return {
    ...state,
    booking: payload,
  };
};

export const updateAction = (state: unknown, payload: unknown): unknown => {
  return payload;
};

export const updateClient = (state: unknown, payload: unknown): unknown => {
  return payload;
};
